// JS IMPORTS
import React, {Component} from 'react';
import {Link} from 'react-router-dom'
import Template from '../components/$Template' // Wrapper component providing encapsulation using shadow DOM API
import MD from '../plugins/MaterialUIComponents' // Material-UI component kit
import FN from '../plugins/Functions' // Abstracted reusable functions
// import Template from '../components/$Template' // Wrapper component providing encapsulation using shadow DOM API
import I from '../plugins/Icons' // Global SVG icon kit

// IMAGES
import aim from '../assets/images/aim.png'
import target from '../assets/images/target.png'


export default class Home extends Component {
  constructor(props) {
    super(props)

    this.id = FN.generateId(3, 4, '-', 'home-')
  }
  
  hideHomePage() {
    //eslint-disable-next-line
    this.props.showNav;
    //eslint-disable-next-line
    this.props.hideHomePage
  }

  render() {
    return (
      <Template>
        <MD.Box
          height='100%'
          pt={{xs:'0px', lg:"4rem"}}
        >
          <MD.Box
            height={{xs:'55vh', lg:'50vh'}}
            align='center'
          >
            <MD.Box
              height={{xs:'55vh', lg:'50vh'}}
              width={{xs:'100%', lg:'30%'}}
              py='20vh'
              id='blur'
            >
              <img src={aim} alt='Aim' height='120rem' width='120rem' />
            </MD.Box>

            <MD.Box
              height={{xs:'55vh', lg:'60vh'}}
              width={{xs:'100%', lg:'30%'}}
              mx='auto'
              top={{xs:'10vh', lg:'12.5vh'}}
              className='target'
            >
              <img src={target} alt='Target' width='100%' height={{xs:'110%', lg:'120%'}} />
            </MD.Box>
          </MD.Box>

          <MD.Box
            className='blue'
            p='0.5vmin'
            mt='0.5rem'
          >
            <MD.Box
              width={{xs:'70%', lg:'30%'}}
              mx='auto'
              pb='1.5rem'
            >
              <h2 className='lightblue blue--text'>My Aim is Clear</h2>
            
              <p className='lightblue--text' align='left'>
                To help you build a strong online presence for your brand,
                removing all obstacles so you can achieve your objectives.
              </p>

              <Link to='/services' onClick={() => {this.props.hideHomePage(); this.props.showNav()}} >
                <MD.Box
                  className='start-btn'
                  width='4rem'
                  m='auto'
                >
                  <I.Pointer className='pointer1 yellow--text' />
                  <I.Pointer className='pointer2 yellow--text' />
                  <I.Pointer className='pointer3 yellow--text' />
                </MD.Box>
              </Link>
            </MD.Box>
          </MD.Box>
        </MD.Box>


        <style>
          {`
              :host {
                overflow-y: auto !important
              }

              #blur {
                position: relative;
                background: radial-gradient(circle,   transparent, rgba(145, 31, 30, 0.75));
                animation: fade-in 10s;
                z-index: 2 !important;
              }

              img[alt = 'Aim'] {
                position: relative;
                animation: move-aim 10s;
                animation-iteration-count: infinite;
                animation-timing-function: ease-in-out;
              }

              .target {
                position: relative;
                transform: translateY(-65vh);
              }

              .start-btn {
                height: 2rem;
                display: flex;
                flex-direction: row;
                align-content: space-around;
              }

              .start-btn svg {
                width: 33%;
                height: 100%;
              }

              .pointer1, .pointer2, .pointer3 {
                animation: swipe 4s;
                animation-iteration-count: infinite;
              }
                
              .pointer2 {animation-delay: 0.07s;}
              
              .pointer3 {animation-delay: 0.14s;}


              /* SM */
              @media only screen and (min-width: 600px) {
                
              }

              /* MD */
              @media only screen and (min-width: 960px) {

              }

              /* LG */
              @media only screen and (min-width: 1280px) {
                
              }

              /* XL */
              @media only screen and (min-width: 1920px) {

              }
          `}
        </style>
      </Template>
    )
  }
}
