// JS Imports
import React, {Component} from 'react';
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom'
import Header from './components/Header'
import HeaderMenu from './components/HeaderMenu'
import AppWindow from './views/AppWindow'

import MDC from './plugins/MaterialUIComponents' // Material-UI component kit
import FN from './plugins/Functions' // Abstracted reusable functions

// CSS Imports
import './layout/app.css';
import './layout/anim.css';
import './layout/normalizer.css'
import './layout/theme.css'


class App extends Component {
  constructor(props) {
    super(props)

    this._headerHeight = ''
    this._mobile = ''
    this._pageHeight = ''
    this.state = {
      showNav: false,
      showNavDrawer: false
    }
    this._topHeader = ''
    this._windowWidth = window.innerWidth
    this._windowHeight = window.innerHeight
    this._setState = FN.setState.bind(this) // bind 'this' to 'setState' function
    this._toggleState = FN.toggle.bind(this) // bind 'this' to 'toggleState' function
  }

  componentDidMount() {
    (async function(ROOT) {
      ROOT._mobile = FN.mobile()
      ROOT._topHeader = document.querySelector('header.top-header')
      await ROOT._topHeader
      ROOT._headerHeight = FN.getComputedStyles(ROOT._topHeader, 'height')
      ROOT._pageHeight = ROOT._windowHeight - ROOT._headerHeight
    })(this)
    
    window.addEventListener('resize', () => FN.resizeHandler(this, FN))

    this.forceUpdate()
  }

  componentDidUpdate() {
    
  }

  componentWillUnmount () {
    window.removeEventListener('resize', FN.resizeHandler(this, FN))
  }


  render() {
    return (
      <Router>
        <div className="App">
          <Header showMenuButton={this.state.showNav} drawer={this._toggleState('showNavDrawer')} />

          <HeaderMenu showIf={this.state.showNavDrawer} />

          <MDC.Box
            minHeight={this._pageHeight}
            className="blue"
            mt='4rem'
          >
            <Switch>
              <Route exact path='/'>
                <AppWindow route='home' oldRouteIndex='' newRouteIndex='' showNav={this._setState('showNav', true)} />
              </Route>

              <Route path='/services'>
                <AppWindow route='services' oldRouteIndex='' newRouteIndex='' />
              </Route>

              <Route path='/blog'>
                <AppWindow route='blog' oldRouteIndex='' newRouteIndex='' />
              </Route>

              <Route path='/profile'>
                <AppWindow route='profile' oldRouteIndex='' newRouteIndex='' />
              </Route>

              <Route path='/contact'>
                <AppWindow route='contact' oldRouteIndex='' newRouteIndex='' />
              </Route>
            </Switch>
          </MDC.Box>
        </div>
      </Router>
    );
  }
}

export default App;
