// Exports a collection of abstracted reusable functions available to use in components with a single import


// use this function to detect mobile and desktop devices
function mobile() {
    let value

    window.innerwidth < window.innerHeight
        ?   value = true
        :   value = false

    return value
}

// I use this function to handle window resize events in App.js
/**
 * @param app : react class object
 * @param helper : functions helper to provide access to other needed functions
 */
function resizeHandler (app, helper) {
    app._mobile = helper.mobile()
    app._windowHeight = window.innerHeight
    app._windowWidth = window.innerWidth
    app._headerHeight = helper.getComputedStyles(app._topHeader, 'height')
    app._pageHeight = app._windowHeight - app._headerHeight
    app.forceUpdate()
}

// use this function to update component state
// to use this function in class based compnents, you must bind it to 'this' in the constructor
function setState(field, newVal) {
    let ROOT = this
    let object = {}
    object[field] = newVal

    return function () {
        ROOT.setState(object)
    }
}

// use this function to toggle binary values
// to use this function in class based compnents, you must bind it to 'this' in the constructor
function toggle(field, state1, state2) {
    let ROOT = this
    let object = {}

    if (state1 && state2) {
        return function () {
            ROOT.state[field] === state1
                ?   object[field] = state2
                :   object[field] = state1

            ROOT.setState(object)
        }
    } else {
        return function () {
            ROOT.setState((state) => {
                object[field] = !state[field]
                return object
            })
        }
    }
}

// GET COMPUTED STYLES FUNCTION
/**
 * @param element => element: HTML DOM element
 * @param property => property: String
 */
function getComputedStyles (element, property) {
    let value
    let style
    if (window.getComputedStyle) {
        style = window.getComputedStyle(element).getPropertyValue(property)
    } else if (document.defaultView) {
        style = document.defaultView(element).getPropertyValue(property)
    }

    // TEST WHETHER THE STYLE IS A STRING OR NUMBER VALUE
    let p = style.split('').reverse().splice(1, 1)  // check if the value ends with 'PX'
    let x = style.split('').reverse().shift(0)  // check if the value ends with 'PX'
    let num = style.split('').reverse().splice(2, 1).join('') * 1

    // eslint-disable-next-line
    if (p == 'p' && x === 'x' && typeof (num) === 'number') {
        value = parseInt(style, 10)
    } else {
        value = style
    }
    return value
}

function getRenderedStylesheet(element) {
    let value

    if (window.getComputedStyle) {
        value = window.getComputedStyle(element)
    } else if (document.defaultView) {
        value = document.defaultView(element)
    }

    return value
}


// generate random ID; this function is used to generate time based numerical IDs
function generateId(fields, fieldLength, separator, prefix) {
    let code
    let hash = []
    let value = prefix

    for(let i=0; i<fields; i++) {
        if(hash.length < fields) {
            makeHash(i)
        }
    }

    function makeHash(fieldIndex) {
        code = Math.random().toString().split('').reverse()

        code.forEach (
            function(value, index, array) {
                if(value !== '0' && (!hash[fieldIndex] || hash[fieldIndex].length < fieldLength)) {
                    if(!hash[fieldIndex]) {
                        hash[fieldIndex] = value
                    } else {
                        hash[fieldIndex] += value
                    }
                } else if (value !== '0' && hash[fieldIndex].length === fieldLength && hash.length !== fields) {
                    hash[fieldIndex] += separator
                }
            }
        )
    }

    function joinHash(array) {
        for(let x=0; x<array.length; x++) {
            value += array[x]
        }

        return value
    }

    joinHash(hash)
    return value
}

const FN = {
    generateId,
    getComputedStyles,
    getRenderedStylesheet,
    mobile,
    resizeHandler,
    setState,
    toggle
}

export default FN
