// JS IMPORTS
import React, {Component} from 'react';
import {Link} from 'react-router-dom'
import MD from '../plugins/MaterialUIComponents' // Material-UI component kit
import Template from './$Template' // Wrapper component providing encapsulation using shadow DOM API
import I from '../plugins/Icons' // Global SVG icon kit

//IMAGE IMPORTS
import logo from '../assets/images/pclogo.png'
import codename from '../assets/images/codename.png'


export default class Header extends Component {
  constructor(props) {
    super(props)

    this.showMenuButton = this.props.showMenuButton
  }

  render() {
    return (
      <Template>
        <MD.AppBar className='top-header blue' display='none'>
          <MD.Box className='header-internal'
            display='flex'
            flexDirection='row'
            justifyContent='space-between'
            width='100%'
            height='100%'
            px='0.5rem'
            py='0.5vmin'
          >
            <MD.Box
              width={{xs: '15%', lg: '5%'}}
              padding='0px'
            >
              <Link to='/'>
                <MD.Avatar className='transparent header-avatar'>
                  <img src={logo} width='100%' alt='logo' />
                </MD.Avatar>
              </Link>
            </MD.Box>

            <MD.Box className='header-center'
              width={{xs: '60%', sm:'40%', lg: '22.5%'}}
              pt={{xs: '0.75rem', sm: '0px'}}
            >
              <Link to='/'>
                <img src={codename}  width='100%' alt='logo' />
              </Link>
            </MD.Box>

            <MD.Box
              width={{xs: '15%', lg: '5%'}}
              padding='0px'
            >
              {
                this.props.showMenuButton
                  ? <MD.Fab className='blue nav-button'  onClick={this.props.drawer}>
                      <I.Menu width="70%" className='white--text' />
                    </MD.Fab>
                  : null
              }
            </MD.Box>
          </MD.Box>
        </MD.AppBar>

        <style>
          {`
              :host {
                height: 4rem;
                flex-direction: row;
              }

              .header-internal a {
                display: block;
                width: 100%;
                height: 100%
              }

              .header-avatar {
                height: 3rem;
                width: 3rem;
                padding-top: 0.5rem;
              }

              .nav-button {
                transform: scale(0.6);
              }


              /* SM */
              @media only screen and (min-width: 600px) {
                
              }

              /* MD */
              @media only screen and (min-width: 960px) {
                .header-avatar {
                  height: 3.5rem;
                  width: 3.5rem;
                  padding: 0px;
                }

                .nav-button {
                  transform: scale(0.75);
                }
              }

              /* LG */
              @media only screen and (min-width: 1280px) {
                
              }

              /* XL */
              @media only screen and (min-width: 1920px) {

              }
          `}
        </style>
      </Template>
    )
  }
}
