// JS Imports
import React, {Component} from 'react';

import Home from './Home'
import Services from './Services'
import Blog from './Blog'
import Profile from './Profile'
import Contact from './Contact'
import BottomNav from '../components/BottomNav'

import Template from '../components/$Template' // Wrapper component providing encapsulation using shadow DOM API
import MD from '../plugins/MaterialUIComponents' // Material-UI component kit
import FN from '../plugins/Functions' // abstracted reusable functions





export default class AppWindow extends Component {
  constructor(props){
    super(props)

    this._route = this.props.route
    this._setState = FN.setState.bind(this) // bind 'this' to 'setState' function
    this.state = {
      landing: true,
      showHomePage: true
    }
    this.panel = null
    this.panelRef = React.createRef()
    this.showHomePage = true
    this.scheduledUpdate = true
  }

  componentDidMount() {
    (async function(ROOT) {
      await ROOT.panelRef.current

      ROOT.panel = ROOT.panelRef.current
      await ROOT.panel
    })(this)
  }

  componentDidUpdate() {
    let ROOT = this

    if(this.props.route === 'home' && this.scheduledUpdate) {
      this.setState({showHomePage: true})
      this.scheduledUpdate = false

      setTimeout(() => {ROOT.scheduledUpdate = true}, 1000)
    } else if (this.landing && this.scheduledUpdate) {
      this.setState({showHomePage: true})
      this.scheduledUpdate = false

      setTimeout(() => {ROOT.scheduledUpdate = true}, 1000)
    } else if (this.scheduledUpdate) {
      this.setState({showHomePage: false})
      this.scheduledUpdate = false

      setTimeout(() => {ROOT.scheduledUpdate = true}, 1000)
    }
  }

  render() {
    return (
      <Template>
        <MD.Box className='app-window'>
          <ul className={`${this.props.route} ${this.showHomePage}`} id={this.panelRef} position='absolute'>
            <li>
              <ul>
                <li>
                  <Services />
                </li>

                <li>
                  <Blog />
                </li>

                <li>
                  <Profile />
                </li>

                <li>
                  <Contact />
                </li>
              </ul>
            </li>

            {
              this.state.showHomePage
              ? <li>
                  <Home hideHomePage={this._setState('landing', false)} showNav={this.props.showNav} />
                </li>

              : null
            }

            <li id='footer-slide'>
              {/* <BottomNav showIf={true} /> */}
            </li>
          </ul>
        </MD.Box>

        <style>
          {`
              :host {
                overflow-x: auto !important;
              }

              ul.true {
                width: 200vw !important;
              }

              ul {
                display: block;
                height: calc(100vh - 4rem);
                width: 100vw;
                padding: 0px;
                margin: 0px;
                overflow: hidden;
                overflow-x: auto !important;
              }

              ul ul {width: 400vw;}

              li {
                display: inline-block;
                height: 100%;
                width: 100vw;
                padding: 0px;
                margin: 0px;
              }

              li li {
                overflow: hidden;
                overflow-y: auto !important;
              }

              li:not(:last-of-type) {
                overflow-x: auto !important;
              }


              /* SM */
              @media only screen and (min-width: 600px) {
                
              }

              /* MD */
              @media only screen and (min-width: 960px) {

              }

              /* LG */
              @media only screen and (min-width: 1280px) {
                
              }

              /* XL */
              @media only screen and (min-width: 1920px) {

              }
          `}
        </style>
      </Template>

      //         .home #footer-slide {
      //           height: 4rem;
      //           width: 100vw;
      //           position: relative;
      //           top: -4rem;
      //           float: left;
      //         }

      //         .services #footer-slide, .blog #footer-slide, .profile #footer-slide, .contact #footer-slide {
      //           height: 4rem;
      //           width: 100vw;
      //           position: relative;
      //           top: -5.5rem;
      //           float: left;
      //         }
    )
  }
}
