// JS IMPORTS
import React, {Component} from 'react';
import {
  // Route,
  Link
} from 'react-router-dom'

// import Template from './Template' // Wrapper component providing encapsulation using shadow DOM API
// import MD from '../plugins/MaterialUIComponents'
import I from '../plugins/Icons' // Global SVG icon kit






export default class BottomNav extends Component {
  constructor(props) {
    super(props)

    this._navList = ''
    this.state = {
      nav: [
        {
          name: 'services',
          route: '/services',
          icon: () => <I.Pointer />,
          className: 'blue--text',
          childRoutes: [
            {
              name: 'software',
              route: '/software',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'graphics',
              route: '/graphics',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'marketing',
              route: '/marketing',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'legal',
              route: '/legal',
              icon: I.Pointer,
              className: 'lightblue'
            }
          ]
        },

        {
          name: 'blog',
          route: '/blog',
          icon: () => <I.Pointer />,
          className: 'blue--text',
          childRoutes: [
            {
              name: 'news',
              route: '/news',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'tutorials',
              route: '/tutorials',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'legal',
              route: '/legal',
              icon: I.Pointer,
              className: 'lightblue'
            }
          ]
        },

        {
          name: 'profile',
          route: '/profile',
          icon: () => <I.Pointer />,
          className: 'blue--text',
          childRoutes: [
            {
              name: 'bio',
              route: '/bio',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'portfolio',
              route: '/portfolio',
              icon: I.Pointer,
              className: 'lightblue'
            },
            
            {
              name: 'github',
              route: '/github',
              icon: I.Pointer,
              className: 'lightblue'
            }
          ]
        },

        {
          name: 'contact',
          route: '/contact',
          icon: () => <I.Pointer />,
          className: 'blue--text',
          childRoutes: [
            {
              name: 'email',
              route: '/email',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'phone',
              route: '/phone',
              icon: I.Pointer,
              className: 'lightblue'
            },
            
            {
              name: 'social media',
              route: '/social-media',
              icon: I.Pointer,
              className: 'lightblue'
            }
          ]
        }
      ]
    }
  }
  componentDidMount () {
    this._navList = this.state.nav.map((item, index) => 
       <li key={index}>
          <Link to={item.route} className={item.className}>
            <div>
              {item.icon()}
            </div>

            {item.name}
          </Link>
        </li>
    )
  }

  render () {
    return (
        <nav id='bottom-nav' className='lightblue'>
          {/* <Template>
            <MDC.Box>
              <div></div>
            </MDC.Box>

            <style>

            </style>

            <style scoped>
              
            </style>
          </Template> */}

          <ul>
            {this._navList}
          </ul>

        <style scoped>
          {`
          
            ul {
                  display: flex;
                  height: 3rem;
                  width: 100vw !important;
                  justify-content: space-between;
                  padding: 3%;
                  padding-top: 0px;
                  padding-bottom: 0px;
                }
          `}
        </style>
      </nav>
    )
  }
} 
