// JS IMPORTS
import React, {Component} from 'react';
// import {
//   Link
// } from 'react-router-dom'

import MDC from '../plugins/MaterialUIComponents' // Material-UI component kit
// import I from '../plugins/Icons' // Global SVG icon kit

export default class HeaderMenu extends Component {
  constructor(props) {
    super(props)

    this._className = this.props.showif ? 'header-menu' : ''
  }

  render() {
    this.props.showIf
      ? this._className = 'header-menu'
      : this._className = ''

    return (
      <MDC.Box>
        {
          this.props.showIf
            ? <MDC.Box className={this._className}>
                <nav>
                  <ul>
                    <p>MENU</p>
                  </ul>
                </nav>
              </MDC.Box>

            : <MDC.Box></MDC.Box>
        }

        <style>
        {`
            .header-menu {
              width: 100vw;
              height: calc(100vh - 7rem);
              position: fixed;
              top: 4rem;
              background: rgba(0, 0, 0, 0.5);
              z-index: 999;
            }

            .header-menu nav {
              height: 100%;
              width: 100%;
            }

            .header-menu ul {
              height: 100%;
              width: calc(10rem + 20%);
              padding: 0px;
              position: relative;
              float: right;
              background: var(--white);
            }
        `}
        </style>
      </MDC.Box>
    )
  }
}
